<template>
    <div>
        <div
            class="box_con"
            tabindex="0"
            @keyup.enter="getContent"
            @keyup.left="transChapter(-1)"
            @keyup.right="transChapter(1)"
            @keyup.down="toDown()"
            @keyup.up="toUp()"
        >
            <div class="bookname">
                <h1>{{ title }}</h1>
                <div class="bottem1">
                    <a href="javascript:;" @click="transChapter(-1)"
                        >上一章&emsp;</a
                    >
                    ←&emsp;
                    <a href="javascript:;" @click="getContent"
                        >章节目录&emsp;</a
                    >
                    →&emsp;
                    <a href="javascript:;" @click="transChapter(1)">下一章</a>
                </div>
            </div>

            <div id="content">
                <p v-for="item in text" :key="item.id">{{ item.context }}</p>
                <div class="bottem1">
                    <a href="javascript:;" @click="transChapter(-1)"
                        >上一章&emsp;</a
                    >
                    ←&emsp;
                    <a href="javascript:;" @click="getContent"
                        >章节目录&emsp;</a
                    >
                    →&emsp;
                    <a href="javascript:;" @click="transChapter(1)">下一章</a>
                </div>
            </div>
        </div>

        <img src="../../assets/arrow-repeat.svg" alt="" ref="wait" id="wait" />

        <button class="goTop-hook" style="display: none">返回顶部</button>
    </div>
</template>

<script>
import bus from "./bus.js";
import axios from "axios";
import $ from "jquery";
export default {
    data() {
        return {
            title: "",
            text: [],
            id: "",
            index: 0,
        };
    },
    methods: {
        toDown() {
            var newScroll = $("html").scrollTop() + 550;
            $("html").animate(
                {
                    scrollTop: newScroll,
                },
                100
            );
        },
        toUp() {
            var newScroll = $("html").scrollTop() - 550;
            $("html").animate(
                {
                    scrollTop: newScroll,
                },
                100
            );
        },
        showWait() {
            this.$refs.wait.style.display = "block";
        },
        disShowWait() {
            this.$refs.wait.style.display = "none";
        },
        backTop() {
            $("html").animate(
                {
                    scrollTop: 0,
                },
                300
            );
        },
        getBook(e) {
            this.showWait();
            this.suspend();
            axios({
                method: "get",
                url: "http://127.0.0.1:80/book",
                params: {
                    id: e,
                },
            }).then(
                (value) => {
                    this.title = value.data.title;
                    this.text = value.data.text;
                    this.id = e;
                    this.disShowWait();
                    this.backTop();
                    this.unsuspend();
                },
                (reason) => {
                    this.disShowWait();
                    this.unsuspend();
                    alert("请求失败,请重试");
                }
            );
        },
        getContent() {
            this.$emit("toContent", "显示目录");
        },
        transChapter(value) {
            bus.$emit("transChapter", this.index + value);
        },
    },
    computed: {},
    mounted() {
        bus.$on("idLink", (e) => {
            this.getBook(e[0]);
            this.index = e[1];
        });
        bus.$on("bakcNewId", (e) => {
            if (e[0] === undefined && this.index > 1) {
                alert("这已经是最新一章，不能再前进啦");
            } else if (e[0] === undefined && this.index < 1) {
                alert("这是第一章，不能再回到上一章啦");
            } else {
                this.getBook(e[0]);

                this.index = e[1];
            }
        });
    },
};
</script>

<style lang="scss" scoped>
@keyframes waitting {
    100% {
        transform: rotate(360deg);
    }
}
body {
    background-color: #e9faff;
    color: #555;
    font-size: 12px;
    margin: 0 auto;
}

.box_con {
    border: 2px solid #88c6e5;
    overflow: hidden;
    width: 976px;
    margin: 10px auto;
    .bookname {
        border-bottom: 1px dashed #88c6e5;
        line-height: 30px;
        padding-top: 10px;
        margin-bottom: 10px;
        h1 {
            font-style: normal;
            font-variant-ligatures: normal;
            font-variant-caps: normal;
            font-variant-numeric: normal;
            font-variant-east-asian: normal;
            font-weight: normal;
            font-stretch: normal;
            font-size: 25px;
            line-height: 35px;
            margin-left: -3em;
            padding-top: 10px;
            text-align: center;
        }
    }
}

.bottem1 {
    clear: both !important;
    text-align: center;
    width: 900px;

    font-size: 12px;
    a {
        color: #085308;
        font-size: 14px;
        margin-left: 10px;
        margin-right: 10px;
        text-decoration: none;
        &:hover {
            color: red;
            text-decoration: underline;
        }
    }
}

#content {
    font-size: 19pt;
    color: #555;
    width: 95%;
    //   padding-left: 1em;
    p {
        margin-left: 1em;
        text-indent: 2em;
    }
}

#wait {
    display: none;
    position: fixed;
    top: 40%;
    left: 50%;
    width: 4vw;
    margin-left: -5.3333vw;
    animation: waitting 1s linear infinite;
}
</style>