import Vue from 'vue'
import App from './App.vue'


Vue.config.productionTip = false

Vue.prototype.suspend = function () {
  document.body.style.pointerEvents = 'none'
}
Vue.prototype.unsuspend = function () {
  document.body.style.pointerEvents = 'auto'
}



new Vue({
  render: h => h(App),
}).$mount('#app')