<template>
  <div class="value">
    <h1>普通个别年金{{fop.name}}计算器</h1>
    <h1>本计算器暂时不支持百分数输入，百分数请转化为小数输入</h1>
    <h1>
      使用方法:
      <h4>
        输入期数→点击：点击生成计算列表→输入每一期的本金→输入小数形式的利率→点击：点击计算
      </h4>
    </h1>

    <input
      type="text"
      id="number"
      style="margin-left: 5px"
      v-model.lazy.number="numberOfPeriods"
      @click="clearInputValue"
      autocomplete='off'
    />
    <input
      type="text"
      style="margin-left: 5px"
      id="interest_rate"
      v-model.lazy.number="rate"
      @click="clearInputValue"
      autocomplete='off'
    />
    <button @click="generate($event)">点击生成计算列表</button>
    <button id="yunsuan" @click="fop.fun">点击计算</button>
    <input
      type="text"
      id="result"
      v-model.lazy.number="result"
      @click="clearInputValue"
      autocomplete='off'
    />
    <br>
    <div v-for="item in targetList" :key="item.id">
      <input
        type="text"
        v-model.lazy.number="item.value"
        @click="clearInputValue"
        autocomplete='off'
      />
    </div>
    <br>
  </div>
</template>

<script>
export default {
  data() {
    return {
      numberOfPeriods: "请输入期数",
      result: "计算结果,结果保留10位小数",
      rate: "请输入利率",
      targetList: [],
    };
  },
  methods: {
    generate($event) {
      for (var i = 0; i < this.numberOfPeriods; i++) {
        this.targetList.push({
          value: "第" + (i + 1) + "期本金",
          id: i + 1,
        });
      }
      $event.target.disabled = true;
    },
    clearInputValue($event) {
      $event.target.value = "";
    },
    futureSum() {
      var sum = 0;
      this.targetList.forEach((element, index) => {
        var divVal =
          element.value *
          (1 + this.rate) ** (this.targetList.length - 1 - index);
        sum = sum + divVal;
      });
      this.result = sum.toFixed(10);
    },
    presentSum() {
      var sum = 0;
      this.targetList.forEach((element, index) => {
        // if(element.value)
        var divVal = element.value / (1 + this.rate) ** (index + 1);
        sum = sum + divVal;
      });
      this.result = sum.toFixed(10);
    },
  },
  props: ["period"],
  computed: {
    fop() {
      if (this.period === "future") {
        return {
          name: "终值",
          fun: this.futureSum,
        };
      } else {
        return {
          name: "现值",
          fun: this.presentSum,
        };
      }
    },
 
  },

};
</script>

<style lang="scss" scoped>
.value {
  input,
  button {
    margin: 5px 5px;
    box-sizing: border-box;
    outline: none;
  }
  div {
    float: left;
  }
  h1 {
    font-size: 1em;
  }
  input:nth-of-type(3){
    width: 14em;
  }
}
</style>


