<template>
    <div id="app">
        <Content @toContext="thisContext" v-show="content"></Content>
        <Book v-show="book" @toContent="thisContent"></Book>
    </div>
</template>

<script>
import value from "./components/Value.vue";
import Book from "@/components/bookfans/book.vue";
import Content from "@/components/bookfans/content.vue";

export default {
    name: "App",
    components: {
        value,
        Book,
        Content,
    },
    data() {
        return {
            book: false,
            content: true,
        };
    },
    methods: {
        thisContext(e) {
            this.book = true;
            this.content = false;
        },
        thisContent() {
            this.book = false;
            this.content = true;
        },
    },

};
</script>

