<template>
    <div class="box_con">
        <h1 class="bookName" id="bookName">{{ title }}</h1>
        <div class="list">
            <dl>
                <span v-for="(item, index) in contentName" :key="index"
                    ><a
                        href="javascript:;"
                        :id="content[index]"
                        @click="getText"
                        :index="index"
                        >{{ item }}</a
                    ></span
                >
            </dl>
        </div>
        <div class="search">
            <input
                type="search"
                name=""
                id=""
                placeholder="请输入要搜索的新书书名，不要缺漏错误"
                v-model.lazy.trim="newBook"
            />
            <button @click="getNewBook">搜索新书</button>
            <img
                src="../../assets/arrow-repeat.svg"
                alt=""
                ref="wait"
                id="wait"
            />
        </div>
    </div>
</template>

<script>
import axios from "axios";
import bus from "./bus.js";
export default {
    data() {
        return {
            content: [],
            contentName: [],
            title: "",
            newBook: "",
        };
    },
    methods: {
        showWait() {
            this.$refs.wait.style.display = "block";
        },
        disShowWait() {
            this.$refs.wait.style.display = "none";
        },
        getContent(book) {
            this.showWait();
            this.suspend()
            axios({
                url: "http://127.0.0.1:80/content",
                method: "get",
                params: {
                    bookname: book,
                },
            }).then(
                (value) => {
                    this.content = value.data.content;
                    this.contentName = value.data.contentName;
                    this.title = value.data.title;
                    localStorage.setItem("bookname", book);
                    this.disShowWait();
                    this.unsuspend()
                },
                (reason) => {
                    this.unsuspend()
                    this.disShowWait();
                    alert("请求失败，请重试");
                }
            );
        },

        getText($event) {
            let id = $event.target.id;
            let index = parseInt($event.target.getAttribute("index"));
            bus.$emit("idLink", [id, index]);
            this.$emit("toContext", true);
        },
        getNewBook() {
            this.getContent(this.newBook);
        },
    },
    props: ["searchBoookName"],
    mounted() {
        bus.$on("transChapter", (e) => {
            let newChapter = this.content[e];

            bus.$emit("bakcNewId", [newChapter, e]);
        });
        if (localStorage.getItem("bookname") !== null) {
            this.getContent(localStorage.getItem("bookname"));
        } else {
            alert('如果目录页为空，可使用右侧搜索新书功能完成初始化即可正常使用')
        }
    },
};
</script>

