    position: relative;
    color: rgb(85, 85, 85);
    font-family: ËÎÌå;
    font-size: 12px;
    padding: 0;
    border: 2px solid #88c6e5;

    width: 976px;
    margin: 10px auto;
}
.list {
    color: rgb(85, 85, 85);
    font-family: ËÎÌå;
    font-size: 12px;
    margin: auto;
    padding: 2px;
    border-top: 1px dashed #88c6e5;
    dl {
        color: rgb(85, 85, 85);
        font-family: ËÎÌå;
        font-size: 12px;
        padding: 0;
        overflow: hidden;
        padding-bottom: 1px;
        margin: auto;
        a {
            color: rgb(85, 85, 85);
            font-family: ËÎÌå;
            font-size: 12px;
            margin: auto;
            padding: 0;
            border-bottom: 1px dashed #ccc;
            display: inline;
            float: left;
            height: 25px;
            line-height: 200%;
            margin-bottom: 5px;
            overflow: hidden;
            text-align: left;
            text-indent: 10px;
            vertical-align: middle;
            width: 33%;
            text-decoration: none;

            &:hover {
                color: red;
                text-decoration: underline;
                cursor: pointer;
            }
        }
    }
}

.bookName {
    margin: 0;
    padding: 0;
    -webkit-text-size-adjust: 100%;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    box-sizing: border-box;
    margin: 0.67em 0;
    font-family: inherit;
